
























import { Component, Prop, Vue } from "vue-property-decorator";

@Component({
  components: {}
})
export default class Speedometer extends Vue {
  @Prop() private percentage!: number;

  get strokeDashArray() {
    if (this.percentage == null) return `stroke-dasharray: 0 18.34 18.34;`;
    return `stroke-dasharray: ${(this.percentage * 18.34) / 100} 18.34 18.34;`;
  }

  get rotation() {
    if (this.percentage == null) return `transform: rotate(0deg);`;
    return `transform: rotate(${(this.percentage * 262) / 100}deg);`;
  }
}
